.relatedproducts{
     display: flex;
     flex-direction: column;
     align-items: center;
     gap: 10px;
     height: 90vh;
}

.relatedproducts h1{
     font-size: 50px;
     color: #171717;
     font-weight: 600;
}

.relatedproducts hr{
     width: 200px;
     height: 6px;
     border-radius: 10px;
     background-color: #252525;
}

.relatedproducts-item{
     display: flex;
     gap: 30px;
     margin-top: 50px;
}